<!--  -->
<template>
  <div class="body">
    <!--  -->
    <orders-form ref="ordersDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="orderNo">
        <el-input v-model="queryForm.orderNo" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="queryForm.name" placeholder="" size="small" clearable/>
      </el-form-item>
<!--      <el-form-item label="" prop="price">-->
<!--        <el-input v-model="queryForm.price" placeholder="" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="" prop="wxno">-->
<!--        <el-input v-model="queryForm.wxno" placeholder="" size="small" clearable/>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="" prop="status">-->
<!--        <el-select v-model="queryForm.status" placeholder="" size="small" clearable>-->
<!--          <el-option v-for="dict in statusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
<!--      <el-form-item label="" prop="userId">-->
<!--        <el-select v-model="queryForm.userId" placeholder="" size="small" clearable>-->
<!--          <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item label="" prop="type">
        <el-select v-model="queryForm.type" placeholder="" size="small" clearable>
          <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['orders:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['orders:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
<!--      <el-table-column label="" prop="orderNo" sortable/>-->
      <el-table-column label="" width="300" show-overflow-tooltip prop="name" sortable/>
      <el-table-column label="" prop="userId" sortable>
        <template slot-scope="scope">
          <span v-for="sysAuthor in sysAuthorDict" v-if="sysAuthor.unionid==scope.row.userId">{{sysAuthor.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="type" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in typeDict" v-if="dict.dictValue==scope.row.type">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="price" sortable/>
<!--      <el-table-column label="" prop="wxno" sortable/>-->
      <el-table-column label="" prop="orderDate" sortable/>
      <el-table-column label="" prop="afterDay" sortable/>
      <el-table-column label="" prop="status" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in statusDict" v-if="dict.dictValue==scope.row.status">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="content" show-overflow-tooltip sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['orders:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['orders:edit']"></el-button>
          <el-button size="mini" type="success" round plain icon="el-icon-edit" @click="finishOrders(scope.row)"  v-hasPermi="['orders:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import ordersForm from './ordersForm'
    export default {
      components: {ordersForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          sysAuthorDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            orderNo: "",
            name: "",
            price: "",
            wxno: "",
            orderDate: "",
            status: "",
            userId: "",
            type: "",
            content: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
        //
        this.getSysAuthorDict();
      },
      computed: {
        //
        statusDict() {
          return this.$store.getters.getDictArray('order_status');
        },
        //
        typeDict() {
          return this.$store.getters.getDictArray('order_type');
        },
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/orders/getPage?delFlag=0', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        getSysAuthorDict() {
          this.$request.doGet('/admin/sysAuthor/getList').then(res => {
            if (res.isOk) {
              this.sysAuthorDict = res.list;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //
        openDialog(option) {
          this.$refs.ordersDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("");
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.delete(id);
            that.showSuccess("");
          });
        },
        finishOrders(row){
          const that = this;
          that.showConfirm("?", function () {
            row.delFlag = 1;
            that.$request.doPost('/admin/orders/sub', row).then(res => {
              if (res.isOk) {
                that.showSuccess("");
                that.getList();
              };
            });
          })
        },
        //
        delete(id) {
          this.$request.doGet('/admin/orders/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



