<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="card" ref="cardForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="card.id"/>
        </el-form-item>
        <el-form-item label="" prop="cardNumber">
          <el-input v-model="card.cardNumber" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="cardType">
          <el-select v-model="card.cardType" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in cardTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userNo">
          <el-select v-model="card.userNo" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cardStatus">
          <el-select v-model="card.cardStatus" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in cardStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="cardPrice">
          <el-input v-model="card.cardPrice" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="cardExpireTime">
          <el-date-picker v-model="card.cardExpireTime" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="cardContent">
          <el-input type="textarea" v-model="card.cardContent" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "cardForm",
    data() {
      return {
        sysAuthorDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        card: {
          cardNumber: "",
          cardType: "",
          userNo: "",
          cardStatus: "",
          cardPrice: "",
          cardExpireTime: "",
          cardContent: ""
        },
        //
        rules: {
          cardNumber: [{required: true, message: '', trigger: 'blur'},{validator:this.$validator.unique,form:this,model:'card',tableName:'card',column:'card_number',message:'',trigger:'blur'}],
          cardType: [{required: true, message: '', trigger: 'blur'}],
          userNo: [{required: true, message: '', trigger: 'blur'}],
          cardStatus: [{required: true, message: '', trigger: 'blur'}],
          cardPrice: [{required: true, message: '', trigger: 'blur'}],
          cardExpireTime: [{required: true, message: '', trigger: 'blur'}],
          cardContent: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      cardTypeDict() {
        return this.$store.getters.getDictArray('card_type');
      },
      //
      cardStatusDict() {
        return this.$store.getters.getDictArray('card_status');
      },
    },
    methods: {
      //
      getSysAuthorDict() {
        this.$request.doGet('/admin/sysAuthor/getList').then(res => {
          if (res.isOk) {
            this.sysAuthorDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysAuthorDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getCard(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getCard(id) {
        this.$request.doGet("/admin/card/get?id=" + id).then(res => {
          if (res.isOk) {
            this.card = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.cardForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/card/sub', that.card).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.cardForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



