<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="orders" ref="ordersForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="orders.id"/>
        </el-form-item>
        <el-form-item label="" prop="orderNo">
          <el-input v-model="orders.orderNo" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="orders.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="orders.price" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="wxno">
          <el-input v-model="orders.wxno" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="orderDate">
          <el-date-picker v-model="orders.orderDate" placeholder="" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="orders.status" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in statusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="orders.userId" placeholder="" :disabled="dialog.disabled">
            <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="orders.type" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input type="textarea" v-model="orders.content" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="works" :refId="orders.id" v-model="orders.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "ordersForm",
    data() {
      return {
        sysAuthorDict:[],
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        orders: {
          orderNo: "",
          name: "",
          price: "",
          wxno: "",
          orderDate: "",
          status: "",
          userId: "",
          type: "",
          content: ""
        },
        //
        rules: {
          orderNo: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          price: [{required: true, message: '', trigger: 'blur'}],
          wxno: [{required: true, message: '', trigger: 'blur'}],
          orderDate: [{required: true, message: '', trigger: 'blur'}],
          status: [{required: true, message: '', trigger: 'blur'}],
          userId: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          content: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      statusDict() {
        return this.$store.getters.getDictArray('order_status');
      },
      //
      typeDict() {
        return this.$store.getters.getDictArray('order_type');
      },
    },
    methods: {
      //
      getSysAuthorDict() {
        this.$request.doGet('/admin/sysAuthor/getList').then(res => {
          if (res.isOk) {
            this.sysAuthorDict = res.list;
          }
        })
      },
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysAuthorDict();//
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getOrders(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getOrders(id) {
        this.$request.doGet("/admin/orders/get?id=" + id).then(res => {
          if (res.isOk) {
            this.orders = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.ordersForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/orders/sub', that.orders).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.ordersForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



