<!--  -->
<template>
  <div class="body">
    <!--  -->
    <card-form ref="cardDialog" @reloadList="getList"/>
    <!--  -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="" prop="cardNumber">
        <el-input v-model="queryForm.cardNumber" placeholder="" size="small" clearable/>
      </el-form-item>
      <el-form-item label="" prop="cardType">
        <el-select v-model="queryForm.cardType" placeholder="" size="small" clearable>
          <el-option v-for="dict in cardTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="userNo">
        <el-select v-model="queryForm.userNo" placeholder="" size="small" clearable>
          <el-option v-for="sysAuthor in sysAuthorDict" :key="sysAuthor.id" :value="sysAuthor.id" :label="sysAuthor.nickName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="cardStatus">
        <el-select v-model="queryForm.cardStatus" placeholder="" size="small" clearable>
          <el-option v-for="dict in cardStatusDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery"></el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()"></el-button>
      </el-form-item>
    </el-form>
    <!--  -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:''})" v-hasPermi="['card:add']"></el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['card:delete']"></el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!--  -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id" prop="id" v-if="false"/>
      <el-table-column label="" type="index" fixed width="50"/>
      <el-table-column label="" prop="cardNumber" sortable/>
      <el-table-column label="" prop="cardType" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in cardTypeDict" v-if="dict.dictValue==scope.row.cardType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="userNo" sortable>
        <template slot-scope="scope">
          <span v-for="sysAuthor in sysAuthorDict" v-if="sysAuthor.unionid==scope.row.userNo">{{sysAuthor.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="cardStatus" sortable>
        <template slot-scope="scope">
          <span :style="dict.style" v-for="dict in cardStatusDict" v-if="dict.dictValue==scope.row.cardStatus">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="cardPrice" sortable/>
      <el-table-column label="" prop="cardExpireTime" sortable/>
      <el-table-column label="" prop="cardContent" show-overflow-tooltip sortable/>
      <el-table-column label="" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'',pageFrom:'view',id:scope.row.id})" v-hasPermi="['card:view']"></el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['card:edit']"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['card:delete']"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import cardForm from './cardForm'
    export default {
      components: {cardForm},
      data() {
        return {
          loading: true,//
          selectedData: [],//
          total: 0, //
          sysAuthorDict: [],
          dataList: [],
          queryForm: {
            current: 1,
            size: 10,
            cardNumber: "",
            cardType: "",
            userNo: "",
            cardStatus: "",
            cardPrice: "",
            cardExpireTime: "",
            cardContent: ""
          }
        };
      },
      mounted() {
        //
        this.getList();
        //
        this.getSysAuthorDict();
      },
      computed: {
        //
        cardTypeDict() {
          return this.$store.getters.getDictArray('card_type');
        },
        //
        cardStatusDict() {
          return this.$store.getters.getDictArray('card_status');
        },
      },
      methods: {
        //
        handleQuery() {
          this.queryForm.current = 1;
          this.getList();
        },
        //
        getList() {
          this.$request.doGet('/admin/card/getPage', this.queryForm).then(res => {
            if (res.isOk) {
              this.loading = false;
              this.dataList = res.obj.records;
              this.total = res.obj.total;
            }
          });
        },
        //
        getSysAuthorDict() {
          this.$request.doGet('/admin/sysAuthor/getList').then(res => {
            if (res.isOk) {
              this.sysAuthorDict = res.list;
            }
          });
        },
        //
        resetQuery() {
          this.$refs['queryForm'].resetFields();
        },
        //
        openDialog(option) {
          this.$refs.cardDialog.open(option);
        },
        //
        handleSizeChange: function (size) {
          this.queryForm.size = size;
          this.getList();
        },
        //
        handleCurrentChange: function (currentPage) {
          this.queryForm.current = currentPage;
          this.getList();
        },
        //
        handleSelectionChange(data) {
          this.selectedData = data;
        },
        //
        handleDeleteAll() {
          const that = this;
          that.showConfirm('?', function () {
            if (that.selectedData.length > 0) {
              that.selectedData.forEach(function (item, index) {
                that.delete(item.id);
              });
              that.showSuccess("");
            } else {
              that.showWarn("");
            }
          });
        },
        //
        handleDelete(id) {
          const that = this;
          that.showConfirm('?', function () {
            that.delete(id);
            that.showSuccess("");
          });
        },
        //
        delete(id) {
          this.$request.doGet('/admin/card/delete?id=' + id).then(res => {
            if (res.isOk) {
              this.getList();
            }
          });
        },
      }
    };
</script>



