<!--  -->
<template>
  <div class="body">
    <el-dialog :title="dialog.title" :close-on-click-modal="false" :visible.sync="dialog.open">
      <el-form :model="works" ref="worksForm" :rules="rules" label-width="100px" class="edit-form">
        <el-form-item label="id" class="hidden" prop="id">
          <el-input type="hidden" v-model="works.id"/>
        </el-form-item>
        <el-form-item label="" prop="picture">
          <sui-photo type="works" v-model="works.picture" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="works.name" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="works.type" placeholder="" :disabled="dialog.disabled" >
            <el-option v-for="dict in typeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="keyWord">
          <el-input v-model="works.keyWord" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="works.price" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="tools">
          <el-input v-model="works.tools" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="environment">
          <el-input v-model="works.environment" placeholder="" :disabled="dialog.disabled" class="sui-input"/>
        </el-form-item>
        <el-form-item label="" prop="introduce">
          <el-input type="textarea" v-model="works.introduce" placeholder="" :disabled="dialog.disabled"/>
        </el-form-item>
        <el-form-item label="" prop="fileList">
          <sui-file type="works" :refId="works.id" v-model="works.fileList" :disabled="dialog.disabled"/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="cancel"> </el-button>
        <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm()"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "worksForm",
    data() {
      return {
        //
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //
        works: {
          picture: "",
          name: "",
          type: "",
          keyWord: "",
          price: "",
          tools: "",
          environment: "",
          introduce: ""
        },
        //
        rules: {
          picture: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          type: [{required: true, message: '', trigger: 'blur'}],
          keyWord: [{required: true, message: '', trigger: 'blur'}],
          price: [{required: true, message: '', trigger: 'blur'}],
          tools: [{required: true, message: '', trigger: 'blur'}],
          environment: [{required: true, message: '', trigger: 'blur'}],
          introduce: [{required: true, message: '', trigger: 'blur'}]
        }
      }
    },
    computed: {
      //
      typeDict() {
        return this.$store.getters.getDictArray('works_type');
      },
    },
    methods: {
      //
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getWorks(option.id);
        }
      },
      //
      cancel: function () {
        this.dialog.open = false;
      },
      //id
      getWorks(id) {
        this.$request.doGet("/admin/works/get?id=" + id).then(res => {
          if (res.isOk) {
            this.works = res.obj;
          }
        });
      },
      //
      submitForm: function () {
        const that = this;
        that.$refs.worksForm.validate((valid) => {
          if (valid) {//
            that.$request.doPost('/admin/works/sub', that.works).then(res => {
              if (res.isOk) {
                that.cancel();
                that.showSuccess("");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //
      showDialog(title, disabled) {
        this.resetForm(this.$refs.worksForm);//
        this.dialog.open = true;//
        this.dialog.title = title;//
        this.dialog.disabled = disabled;//
      }
    }
  }
</script>

<style scoped>

</style>



